}

@keyframes floatImage4 {
  0% {
    transform: translate(0, 0) rotate(0deg) scale(1);
    filter: brightness(1.15) saturate(1.3) contrast(1.1);
  }
  13% {
    transform: translate(-95px, -80px) rotate(-7deg) scale(1.08);
    filter: brightness(1.24) saturate(1.45) contrast(1.18);
  }
  27% {
    transform: translate(-110px, 55px) rotate(8deg) scale(1.05);
    filter: brightness(1.21) saturate(1.4) contrast(1.15);
  }
  41% {
    transform: translate(-50px, 105px) rotate(-6deg) scale(1.03);
    filter: brightness(1.18) saturate(1.35) contrast(1.12);
  }
  55% {
    transform: translate(90px, 80px) rotate(7deg) scale(1.09);
    filter: brightness(1.25) saturate(1.46) contrast(1.19);
  }
  69% {
    transform: translate(130px, -40px) rotate(-5deg) scale(1.04);
    filter: brightness(1.19) saturate(1.36) contrast(1.13);
  }
  83% {
    transform: translate(40px, -95px) rotate(8deg) scale(1.1);
    filter: brightness(1.26) saturate(1.48) contrast(1.2);
  }
  96% {
    transform: translate(-25px, -65px) rotate(-4deg) scale(1.06);
    filter: brightness(1.2) saturate(1.38) contrast(1.14);
  }
  100% {
    transform: translate(0, 0) rotate(0deg) scale(1);
    filter: brightness(1.15) saturate(1.3) contrast(1.1);
  }
}

/* Workflow Connection Lines */
.workflow-connections {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  pointer-events: none;
  overflow: visible;
}

.connection-line {
  fill: none;
  stroke-width: 3;
  stroke-linecap: round;
  stroke-linejoin: round;
  opacity: 0.5;
  filter: url(#glow) drop-shadow(0 0 8px rgba(102, 126, 234, 0.6));
  animation: pulse-line 3s ease-in-out infinite;
  stroke-dasharray: 10 5;
  stroke-dashoffset: 0;
  transition: all 0.3s ease;
}

.connection-line:hover {
  opacity: 0.8;
  stroke-width: 4;
  filter: url(#glow) drop-shadow(0 0 16px rgba(102, 126, 234, 0.9));
}

/* Animated dash effect */
.line-1-2,
.line-4-3 {
  animation: dash-flow 20s linear infinite, pulse-line 3s ease-in-out infinite;
}

.line-2-4,
.line-3-1 {
  animation: dash-flow 25s linear infinite reverse, pulse-line 3s ease-in-out infinite;
  animation-delay: 1s;
}

.line-1-4,
.line-2-3 {
  opacity: 0.3;
  stroke-width: 2;
  animation: dash-flow 30s linear infinite, pulse-line 4s ease-in-out infinite;
  animation-delay: 2s;
}

@keyframes dash-flow {
  to {
    stroke-dashoffset: -100;
  }
}

@keyframes pulse-line {
  0%, 100% {
    opacity: 0.5;
    stroke-width: 3;
  }
  50% {
    opacity: 0.7;
    stroke-width: 3.5;
  }
}

/* Flow Particles */
.flow-particle {
  filter: drop-shadow(0 0 8px currentColor);
  opacity: 0.9;
  animation: particle-glow 2s ease-in-out infinite;
}

@keyframes particle-glow {
  0%, 100% {
    opacity: 0.7;
    r: 4;
  }
  50% {
    opacity: 1;
    r: 6;
  }
}

/* Node Indicators */
.node-indicator {
  position: absolute;
  top: -15px;
  right: -15px;
  width: 40px;
  height: 40px;
  z-index: 10;
  pointer-events: none;
}

.node-pulse {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(102, 126, 234, 0.6), transparent 70%);
  animation: node-pulse-animation 2s ease-out infinite;
}

@keyframes node-pulse-animation {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(2);
    opacity: 0;
  }
}

.node-dot {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  box-shadow:
    0 0 0 3px rgba(255, 255, 255, 0.3),
    0 0 20px rgba(102, 126, 234, 0.8),
    inset 0 2px 4px rgba(255, 255, 255, 0.5);
  animation: node-dot-pulse 2s ease-in-out infinite;
}

@keyframes node-dot-pulse {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow:
      0 0 0 3px rgba(255, 255, 255, 0.3),
      0 0 20px rgba(102, 126, 234, 0.8),
      inset 0 2px 4px rgba(255, 255, 255, 0.5);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
    box-shadow:
      0 0 0 5px rgba(255, 255, 255, 0.5),
      0 0 30px rgba(102, 126, 234, 1),
      inset 0 2px 4px rgba(255, 255, 255, 0.7);
  }
}

.node-label {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg,
    rgba(102, 126, 234, 0.95),
    rgba(118, 75, 162, 0.95));
  color: white;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 10px;
  font-weight: 700;
  white-space: nowrap;
  box-shadow:
    0 4px 12px rgba(102, 126, 234, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.bg-image:hover .node-label {
  opacity: 1;
}

.bg-image:hover .node-indicator {
  animation: node-indicator-bounce 0.6s ease;
}

@keyframes node-indicator-bounce {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
}

/* Node specific colors */
.image-1 .node-dot {
  background: linear-gradient(135deg, #667eea, #764ba2);
}

.image-2 .node-dot {
  background: linear-gradient(135deg, #f093fb, #764ba2);
}

.image-3 .node-dot {
  background: linear-gradient(135deg, #764ba2, #667eea);
}

.image-4 .node-dot {
  background: linear-gradient(135deg, #667eea, #f093fb);
}

.image-1 .node-label {
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.95), rgba(118, 75, 162, 0.95));
}

.image-2 .node-label {
  background: linear-gradient(135deg, rgba(240, 147, 251, 0.95), rgba(118, 75, 162, 0.95));
}

.image-3 .node-label {
  background: linear-gradient(135deg, rgba(118, 75, 162, 0.95), rgba(102, 126, 234, 0.95));
}

.image-4 .node-label {
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.95), rgba(240, 147, 251, 0.95));
}

/* Gradient Overlay */
.gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background:
    radial-gradient(ellipse at 30% 40%, rgba(102, 126, 234, 0.15) 0%, transparent 50%),
    radial-gradient(ellipse at 70% 60%, rgba(240, 147, 251, 0.12) 0%, transparent 50%),
    radial-gradient(circle at center, transparent 30%, rgba(26, 26, 46, 0.3) 100%);
  z-index: 7;
  pointer-events: none;
  animation: overlayPulse 15s ease-in-out infinite;
}

@keyframes overlayPulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

/* Floating Gradient Orbs */
.floating-images::before {
  content: '';
  position: absolute;
  width: 700px;
  height: 700px;
  background: radial-gradient(circle, rgba(102, 126, 234, 0.2), transparent 70%);
  border-radius: 50%;
  top: -250px;
  left: -250px;
  filter: blur(100px);
  animation: floatOrb1 30s ease-in-out infinite;
  z-index: 1;
}

.floating-images::after {
  content: '';
  position: absolute;
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(240, 147, 251, 0.18), transparent 70%);
  border-radius: 50%;
  bottom: -200px;
  right: -200px;
  filter: blur(100px);
  animation: floatOrb2 35s ease-in-out infinite;
  z-index: 1;
}

@keyframes floatOrb1 {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(80px, -60px) scale(1.15);
  }
  66% {
    transform: translate(-50px, 50px) scale(0.9);
  }
}

@keyframes floatOrb2 {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  40% {
    transform: translate(-70px, 60px) scale(1.1);
  }
  70% {
    transform: translate(60px, -40px) scale(0.95);
  }
}

/* Animated Light Rays */
.light-rays {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 8;
  pointer-events: none;
  opacity: 0.6;
}

.ray {
  position: absolute;
  width: 3px;
  height: 100%;
  background: linear-gradient(to bottom, transparent, rgba(255, 255, 255, 0.4), transparent);
  animation: rayMove 10s linear infinite;
  filter: blur(1px);
}

.ray-1 {
  left: 25%;
  animation-delay: 0s;
}

.ray-2 {
  left: 50%;
  animation-delay: 3s;
}

.ray-3 {
  left: 75%;
  animation-delay: 6s;
}

@keyframes rayMove {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    transform: translateY(100%);
    opacity: 0;
  }
}

/* Floating Particles */
.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 5;
  pointer-events: none;
}

.particle {
  position: absolute;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.3));
  border-radius: 50%;
  animation: particle-float linear infinite;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

@keyframes particle-float {
  0% {
    transform: translateY(100vh) translateX(0) scale(0);
    opacity: 0;
  }
  10% {
    opacity: 1;
    transform: translateY(90vh) translateX(10px) scale(1);
  }
  50% {
    opacity: 0.8;
    transform: translateY(50vh) translateX(-30px) scale(1.2);
  }
  90% {
    opacity: 0.5;
  }
  100% {
    transform: translateY(-10vh) translateX(50px) scale(0.8);
    opacity: 0;
  }
}

/* Login Container */
.login-container-wrapper {
  position: relative;
  z-index: 100;
  width: 100%;
  max-width: 480px;
  padding: 20px;
  animation: slideUp 0.8s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Glass Card */
.login-glass-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 32px;
  padding: 48px 40px;
  box-shadow:
    0 20px 60px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.5) inset;
  position: relative;
  overflow: hidden;
}

.login-glass-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #667eea, #764ba2, #f093fb);
  animation: gradient-shift 3s ease infinite;
  background-size: 200% 100%;
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

/* Logo Section */
.logo-section {
  text-align: center;
  margin-bottom: 40px;
}

.logo-circle {
  width: auto;
  height: auto;
  margin: 0 auto 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-image {
  width: 200px;
  height: auto;
  max-width: 100%;
}

.welcome-title {
  font-size: 28px;
  font-weight: 800;
  color: #1a202c;
  margin-bottom: 8px;
  line-height: 1.2;
}

.gradient-text {
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-subtitle {
  font-size: 15px;
  color: #718096;
  font-weight: 500;
}

/* Form */
.modern-login-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.input-icon {
  position: absolute;
  left: 18px;
  color: #a0aec0;
  font-size: 18px;
  z-index: 2;
  transition: color 0.3s;
}

.modern-input {
  width: 100%;
  padding: 16px 20px 16px 52px;
  font-size: 16px;
  border: 2px solid #e2e8f0;
  border-radius: 16px;
  background: #f7fafc;
  color: #1a202c;
  transition: all 0.3s;
  outline: none;
  font-family: inherit;
}

.modern-input:focus {
  border-color: #667eea;
  background: #fff;
  box-shadow: 0 0 0 4px rgba(102, 126, 234, 0.1);
}

.modern-input:focus + .input-icon {
  color: #667eea;
}

.modern-input.has-error {
  border-color: #f56565;
  background: #fff5f5;
}

.password-toggle {
  position: absolute;
  right: 18px;
  background: none;
  border: none;
  color: #a0aec0;
  cursor: pointer;
  padding: 8px;
  font-size: 18px;
  transition: color 0.3s;
  z-index: 2;
}

.password-toggle:hover {
  color: #667eea;
}

.error-text {
  font-size: 14px;
  color: #f56565;
  display: flex;
  align-items: center;
  gap: 6px;
  padding-left: 4px;
}

/* Form Options */
.form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -8px;
}

.remember-checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.remember-checkbox input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  width: 20px;
  height: 20px;
  border: 2px solid #cbd5e0;
  border-radius: 6px;
  background: #f7fafc;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.checkmark::after {
  content: '\f00c';
  font-family: 'Font Awesome 6 Free';
  font-weight: 900;
  font-size: 12px;
  color: #fff;
  opacity: 0;
  transform: scale(0);
  transition: all 0.2s;
}

.remember-checkbox input:checked ~ .checkmark {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-color: #667eea;
}

.remember-checkbox input:checked ~ .checkmark::after {
  opacity: 1;
  transform: scale(1);
}

.checkbox-label {
  font-size: 14px;
  color: #4a5568;
  font-weight: 500;
}

.forgot-link {
  font-size: 14px;
  color: #667eea;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.forgot-link:hover {
  color: #764ba2;
  text-decoration: underline;
}

/* Error Alert */
.error-alert {
  padding: 14px 18px;
  background: #fff5f5;
  border: 2px solid #feb2b2;
  border-radius: 12px;
  color: #c53030;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
  animation: shake 0.5s;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-10px); }
  75% { transform: translateX(10px); }
}

/* Login Button */
.login-button {
  width: 100%;
  padding: 16px 24px;
  font-size: 17px;
  font-weight: 700;
  color: #fff;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border: none;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.4);
  position: relative;
  overflow: hidden;
}

.login-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.login-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 28px rgba(102, 126, 234, 0.5);
}

.login-button:hover:not(:disabled)::before {
  left: 100%;
}

.login-button:active:not(:disabled) {
  transform: translateY(0);
}

.login-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.button-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.spinner {
  width: 18px;
  height: 18px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top-color: #fff;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Divider */
.divider {
  position: relative;
  text-align: center;
  margin: 8px 0;
}

.divider::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: #e2e8f0;
}

.divider span {
  position: relative;
  background: rgba(255, 255, 255, 0.95);
  padding: 0 16px;
  color: #a0aec0;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Guest Button */
.guest-button {
  width: 100%;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  color: #4a5568;
  background: #f7fafc;
  border: 2px solid #e2e8f0;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.guest-button:hover:not(:disabled) {
  background: #edf2f7;
  border-color: #cbd5e0;
  transform: translateY(-2px);
}

.guest-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Card Footer */
.card-footer {
  margin-top: 32px;
  padding-top: 24px;
  border-top: 1px solid #e2e8f0;
  text-align: center;
}

.cookies-link {
  background: none;
  border: none;
  color: #718096;
  font-size: 14px;
  cursor: pointer;
  transition: color 0.3s;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 8px;
}

.cookies-link:hover {
  color: #667eea;
  background: #f7fafc;
}

/* Decorative Elements */
.decorative-elements {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.deco-circle {
  position: absolute;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  animation: float 15s ease-in-out infinite;
}

.circle-1 {
  width: 100px;
  height: 100px;
  top: -50px;
  right: -50px;
}

.circle-2 {
  width: 150px;
  height: 150px;
  bottom: -75px;
  left: -75px;
  animation-delay: 3s;
}

.circle-3 {
  width: 80px;
  height: 80px;
  top: 50%;
  right: -40px;
  animation-delay: 6s;
}

/* Responsive */
@media (max-width: 1024px) {
  .image-1 {
    width: 190px;
    left: 2%;
    top: 10%;
  }

  .image-1 img {
    max-height: 260px;
  }

  .image-2 {
    width: 200px;
    right: 2%;
    top: 8%;
  }

  .image-2 img {
    max-height: 280px;
  }

  .image-3 {
    width: 170px;
    bottom: 6%;
    left: 6%;
  }

  .image-3 img {
    max-height: 230px;
  }

  .image-4 {
    width: 180px;
    bottom: 7%;
    right: 5%;
  }

  .image-4 img {
    max-height: 240px;
  }

  .animated-background::before {
    width: 500px;
    height: 500px;
  }

  .animated-background::after {
    width: 400px;
    height: 400px;
  }
}

@media (max-width: 768px) {
  .connection-line {
    stroke-width: 2;
  }

  .line-1-4,
  .line-2-3 {
    opacity: 0.2;
    stroke-width: 1.5;
  }

  .node-indicator {
    width: 32px;
    height: 32px;
  }

  .node-dot {
    width: 12px;
    height: 12px;
  }

  .node-label {
    font-size: 9px;
    padding: 3px 8px;
  }

  .image-1 {
    width: 150px;
    left: 2%;
    top: 12%;
    opacity: 0.28;
  }

  .image-1 img {
    max-height: 200px;
  }

  .image-2 {
    width: 160px;
    right: 2%;
    top: 10%;
    opacity: 0.28;
  }

  .image-2 img {
    max-height: 220px;
  }

  .image-3 {
    width: 130px;
    bottom: 8%;
    left: 4%;
    opacity: 0.28;
  }

  .image-3 img {
    max-height: 180px;
  }

  .image-4 {
    width: 140px;
